@font-face {
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-family: "Jaapokkisubtract";
  src: url("../app/fonts/jaapokkisubtract.woff2");
}
@font-face {
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue";
  src: url("../app/fonts/helveticaneue.woff2");
}
@font-face {
  font-weight: 300;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Light";
  src: url("../app/fonts/helveticaneue-light.woff2");
}
@font-face {
  font-weight: 500;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Medium";
  src: url("../app/fonts/helveticaneue-medium.woff2");
}
@font-face {
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Bold";
  src: url("../app/fonts/helveticaneue-bold.woff2");
}
:root {
  --font-body: "HelveticaNeue", sans-serif;
  --font-title: "Jaapokkisubtract", sans-serif;
  --transition: .2s;
}

:root {
  --brand: hsl(229, 86%, 58%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 10%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  color: var(--text);
  letter-spacing: 0.1rem;
  font-family: var(--font-body);
}

.arrow {
  position: relative;
  display: inline-block;
  min-width: 3.3125rem;
  min-height: 2.0625rem;
  background: url("../images/tools/arrow-right.svg");
}
.arrow__left {
  margin-right: 2.625rem;
  background-image: url("../images/tools/arrow-light.svg");
}

.direction {
  min-height: 12.5rem;
  margin-bottom: 17.1875rem;
}
.direction__inner {
  display: block;
  position: relative;
  top: 0.625rem;
  left: 50%;
  max-width: 3.375rem;
  transform: translateX(-50%) rotate(90deg);
}
.direction__inner::before {
  content: "brws";
  position: absolute;
  right: 3.25rem;
  top: 50%;
  font-size: 1.5rem;
  font-family: var(--font-title);
  transform: translateY(-50%) rotate(-90deg);
}
.direction__inner::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 50%;
  left: -3.75rem;
  min-width: 6.25rem;
  min-height: 6.75rem;
  transform: translateY(-50%);
  background-color: var(--white);
  box-shadow: 0 1.25rem 2.5rem var(--black);
}
.direction__inner svg {
  fill: var(--black);
}
.direction__border {
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 2.8125rem;
  min-width: 0.1875rem;
  min-height: 30.9375rem;
  pointer-events: none;
  transform: translateY(-50%);
  background-color: var(--black);
}
.direction__border::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 4.375rem;
  min-height: 11.125rem;
  transform: translate(-50%, -50%);
  border: 0.625rem solid var(--black);
  background-color: var(--white);
}
.direction__border::after {
  content: "";
  position: absolute;
  top: 9.9125rem;
  left: 2.25rem;
  min-height: 135%;
  min-width: 7.0625rem;
  border-top: 0.1875rem solid;
  border-right: 0.1875rem solid;
  border-color: var(--black);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-title);
}

p {
  margin: 0;
}

a,
a:visited,
a:active {
  color: var(--black);
  text-decoration: none;
  transition: var(--transition) ease-in-out;
}

.container {
  max-width: 83.75rem;
}
.container, .container--small {
  max-width: 73.75rem;
  padding: 0 1.25rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container, .container--small {
    transform: translate(0, 0);
    padding: 0;
  }
}

.header {
  background-image: linear-gradient(var(--brand) 50%, var(--black) 50%);
}
.header, .header a {
  color: var(--white);
  font-weight: 700;
}
.header__inner {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.375rem 0;
}
.header__menu :not(:last-child) {
  margin-right: 1.875rem;
  float: left;
}
.header__logo {
  transform: translate(-50%);
}
.header__main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
}
.header__promo {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 0;
  min-height: 43.75rem;
  background-image: url("../images/header.webp");
}
.header__promo::before {
  content: "01";
  font-size: 4.375rem;
  color: var(--black);
  position: absolute;
  top: 2.5rem;
  left: -3.75rem;
  padding: 0.9375rem 1.25rem 0;
  background-color: var(--white);
  font-family: var(--font-title);
}
.header__promo::after {
  content: "";
  position: absolute;
  top: 11.625rem;
  left: -1.625rem;
  min-width: 0.1875rem;
  min-height: 3.125rem;
  background-color: var(--white);
}
.header__buttons {
  position: absolute;
  top: 5.625rem;
  left: 6.875rem;
  font-size: 0;
}
.header__buttons .arrow::before {
  content: "";
  position: absolute;
  top: -0.625rem;
  left: -0.625rem;
  z-index: -1;
  min-width: 4.6875rem;
  min-height: 1.6875rem;
  background-color: var(--brand);
}
.header__text {
  margin-left: 6.25rem;
  font-size: 1.75rem;
}
.header__text h1 {
  font-size: 5.375rem;
  letter-spacing: 0.5rem;
  position: relative;
}
.header__text h1::before {
  content: "";
  position: absolute;
  top: -1.4375rem;
  left: -2.3125rem;
  min-height: 0.1875rem;
  min-width: 6.25rem;
  background-color: var(--white);
}
.header__text h1::after {
  content: "";
  position: absolute;
  left: 3.125rem;
  bottom: -3.4375rem;
  min-height: 0.1875rem;
  min-width: 12.5rem;
  background-color: var(--white);
}
.header__text p {
  margin-top: 6.25rem;
}
.header__text p .arrow {
  top: 0.375rem;
  margin-right: 1.25rem;
}
.header__text p b {
  font-family: "HelveticaNeue Bold";
}
.header__aside {
  position: absolute;
  right: -7.5rem;
  bottom: 17.5rem;
  transform: rotate(90deg);
}
.header__aside::before {
  content: url("../images/tools/grid.svg");
  position: absolute;
  height: 2.8125rem;
  width: 2.8125rem;
  top: -0.75rem;
  left: -7.8125rem;
}
.header__aside::after {
  content: "01";
  position: absolute;
  top: -1.375rem;
  right: -5.3125rem;
  padding-top: 2.6875rem;
  border-top: 0.1875rem solid var(--white);
  transform: rotate(-90deg);
}

.about__inner {
  display: flex;
  font-family: "HelveticaNeue Bold";
}
.about__inner > * {
  display: flex;
  max-width: 25%;
}
.about__inner > *:last-child {
  flex-direction: column;
}
.about__nav {
  font-size: 0.8125rem;
  max-height: 2.9375rem;
  position: relative;
  right: -11.25rem;
  left: -11.25rem;
}
.about__nav, .about__nav ~ .about__text p {
  transform: rotate(-90deg);
}
.about__menu {
  display: flex;
  gap: 1.5625rem;
  text-transform: uppercase;
}
.about__menu :first-child {
  list-style-type: none;
}
.about__menu a {
  padding: 1.0625rem;
  transition: var(--transition);
}
.about__menu a:hover {
  background-color: var(--brand);
  color: var(--white);
}
.about__image {
  position: relative;
}
.about__image::before {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  top: -3.125rem;
  left: -3.125rem;
  min-width: 19.6875rem;
  min-height: 19.6875rem;
  border: 1.25rem solid var(--brand);
}
.about__text {
  position: relative;
}
.about__text * {
  position: absolute;
  top: -3.125rem;
  left: 1.25rem;
  white-space: nowrap;
}
.about__text p {
  top: 10rem;
  left: -10rem;
}